.projects {
  scroll-behavior: smooth;
}

.projects__heading {
  text-align: center;
  margin: 20px 0;
  font-size: 60px;
}

.projects__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

@media (max-width: 1200px) {
  .projects__list {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 800px) {
  .projects__heading {
    font-size: 40px;
  }
}
